<template>
    <div id="app">
        <div class="speed_dial">
            <md-speed-dial class="md-bottom-right" md-direction="top" md-event="click">
                <md-speed-dial-target class="speed_dial_btn dial_front">
                    <md-icon class="md-morph-initial dial_icon">add</md-icon>
                    <md-icon class="md-morph-final dial_icon">close</md-icon>
                </md-speed-dial-target>

                <md-speed-dial-content class="dial_front">
                    <md-button class="md-icon-button dial_btn" @click=on_language_btn_clicked>
                        <md-tooltip md-direction="left">{{lang_tt[language]}}</md-tooltip>
                        <md-icon :md-src=language_icon class="icon"></md-icon>
                    </md-button>

                    <md-button class="md-icon-button dial_btn" @click=email_btn_pressed()>
                        <md-tooltip md-direction="left">{{msg_tt[language]}}</md-tooltip>
                        <md-icon class="icon">email</md-icon>
                    </md-button>
                </md-speed-dial-content>
            </md-speed-dial>
        </div>

        <div class="parallax_container particle_container">
            <parallax :fixed="true" class="particle_parallax">
                <vue-particles  class="particles"
                                color="#CACACA"
                                :particleOpacity="0.8"
                                :particlesNumber="300"
                                shapeType="circle"
                                :particleSize="3"
                                linesColor="#979797"
                                :linesWidth="1"
                                :lineLinked="true"
                                :lineOpacity="0.4"
                                :linesDistance="150"
                                :moveSpeed="2"
                                :hoverEffect="true"
                                hoverMode="grab"
                                :clickEffect="true"
                                clickMode="push"/>
            </parallax>
        </div>

        <section id="intro">
            <Intro :language="language"/>
        </section>
        <section id="projects" data-aos="fade-up" data-aos-once="true">
            <Projects :language="language"/>
        </section>
    </div>
</template>

<script>
import Intro from './components/Introduction.vue'
import Projects from './components/Projects.vue'
import Parallax from "vue-parallaxy";


export default {
  name: 'App',
  data() {
      return{
          language: 'en',
          language_icon: require('@/assets/de.svg'),
          showDialog: false,

          lang_tt: {'en': 'Change Language',
                   'de': 'Sprache ändern'},

          msg_tt: {'en': 'Message Me',
                   'de': 'Schreib mir!'},
          email_subject: {'en': 'Request from Website',
                          'de': 'Anfrage von Website'},

          email_body: {'en': 'Hi Flo, your website made me curious and I would like to get in contact with you.',
                       'de': 'Hi Flo, deine Website hat mich neugierig gemacht und ich würde gerne mit dir in Kontakt treten.'},
          info: true,
      }
  },
  created() {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    on_language_btn_clicked(){
        if (this.language == 'en'){
            this.language = 'de';
            console.log(this.language);
            this.language_icon = require('@/assets/en.svg');
        }else {
            this.language = 'en';
            console.log(this.language);
            this.language_icon = require('@/assets/de.svg');
        }
    },
    on_impressum_clicked(){
        this.showDialog = !this.showDialog;
        this.info = !this.info;
    },
      email_btn_pressed(){
          window.location.href = 'mailto:florian.lay@gmx.net?subject=' + this.email_subject[this.language]
              + '&body='+ this.email_body[this.language];
      }
  },
  components: {
    Intro,
    Projects,
    Parallax
  },
}
</script>

