<template>
    <div class="project_roboy">
        <div class="absolute_container">
            <div class="roboy">
                <video-player :options="videoOptions" class="video_" ref="player_">
                </video-player>
            </div>
            <div class="pause_play" @click="pause_play_button_clicked">
                <img :src="pause_play_button_path" class="pause_img">
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                player: null,
                videoOptions: {
                    autoplay: true,
                    controls: false,
                    muted: true,
                    loop: true,
                    fluid: true,
                    playsinline: true,
                    sources: [
                        {
                            src:require('@/assets/roboy.mp4'),
                            type: "video/mp4"
                        }]
                },
                pause: false,

                pause_play_button_path: require('@/assets/pause.svg'),
                pause_play_clicked: false,
            }
        },
        mounted(){
            this.player = this.$refs.player_;
        },
        methods: {
            pause_play_button_clicked(){
                if (this.pause_play_clicked){
                    this.pause_play_button_path = require('@/assets/pause.svg');
                    this.pause = false;
                    this.player.$refs.video.play();
                }
                else {
                    this.pause_play_button_path = require('@/assets/play.svg');
                    this.pause = true;
                    this.player.$refs.video.pause();
                }
                this.pause_play_clicked = !this.pause_play_clicked;
            }
        }
    }
</script>

<style>
    .roboy{
    }
    .project_roboy{
        width: 100%;
        display: block;
    }
    .absolute_container{
        display: block;
    }
    .pause_img{
        width: 100%;
    }
    .pause_play{
        position: absolute;
        width: 1vw;
        bottom: 2vh;
        left: 2vh;
        filter: drop-shadow( 1px 1px 1px rgba(0, 0, 0, .5));
        display: inline-block;
    }
    .pause_play:hover {
        filter: drop-shadow(1px 1px 1px rgba(46, 65, 202, 0.25));
    }
</style>